<template>

<main class=" ">
  <div class="listanswer" >
    <table class="table table-hover">
      <thead>
      <tr>
        <th scope="col">답변 내용 </th>
        <th scope="col">공개 여부</th>
      </tr>
     </thead>
    <tbody>
      <tr v-for="(list, i) in state.lists" :key="(list, i)">
        <td>
          <RouterLink :to="{name: 'answerread', params:{id:list.answerid}}"  style="color:rgba(6, 90, 92, 0.9); text-decoration:none; font-weight:bold;" >
            {{ list.content }}
          </RouterLink>
        </td>
        <td>
          <div class="form-check form-switch form-check-inline"  >
            <input v-if="!list.answerPublic" class="form-check-input text-end" type="checkbox" role="switch" id="flexSwitchCheckDefault"  @change="sendValues(list.answerid, list.answerPublic, list.content, list.rate)">
            <input v-else class="form-check-input text-center" type="checkbox" role="switch" id="flexSwitchCheckChecked" checked  @change="sendValues(list.answerid, list.answerPublic, list.content, list.rate)">
          </div>
        </td>
       </tr>
     </tbody>
   </table>

 </div>


</main>
</template>


<script>
import {useRouter} from 'vue-router';
import { reactive, ref } from 'vue'
import axios from 'axios'
import { onMounted } from 'vue'

 export default {
   setup(){
    onMounted(() => { loginHandler(); })

  const router=useRouter();
  const value = ref(false)

  const state = reactive({
    questionid :'',
    lists: '',
    email: sessionStorage.getItem('email'),
    token: sessionStorage.getItem('TOKEN'),
  });

  const values = reactive({
      answerid: '',
      answerPublic:'',
      content:'',
      rate:'',
    })

    const sendValues = (answerid,answerPublic,content, rate) => {
      values.answerid = answerid
      values.answerPublic = answerPublic
      values.content = content,
      values.rate = rate,
      changeSwitchOption()
    };


  const loginHandler = async () => {
    // console.log("PrivateAnswer by Email launch")
  const url= "http://43.201.60.125/api/answerlistbyemail"

  const headers = {
    //TODO send the right amound of tokent and receive the same...
    'Content-Type' : 'application/json',
    Authorization: state.token,
    token: state.token,
    'name': 'test list'
  }
    const body = { email: state.email }
    await axios.post(url, body, { headers }).then(function (res) {
      if(res.status==200){
        state.lists = res.data
      } else {
        alert('bad.')
      }
    })
  }

  const changeSwitchOption = async () => {
      console.log("modify QuestionPublic launch")
      const url = "http://43.201.60.125/api/switchanswerpulic"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        answerid: values.answerid,
        content: values.content,
        answerPublic: !values.answerPublic,
        rate: values.rate,
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('modify successfully')
      } else {
        alert('fail')
      }
    }


  return{  value, state, values, sendValues, loginHandler, changeSwitchOption  }

  }

};


  </script>

  <style scoped>
.checkbox-in-the-middle{
  padding-left: 13em;
 }
  .answer_collapse{
  border: solid 1px grey;
  padding: 0 10px;
  margin: 10px 0;
  }


  .answer{
  display: flex;
  }

  .counter{
    display:  flex;
    align-items: center;
    height: 80px;
    margin-right: 30px;
    background-color:lightgray;
    padding: 0 10px;
    border-radius: 30%;
  }



  .num{
    font-size: 30px;
    color: rgb(6, 90, 92);
    font-weight: bold;
    margin: 10px;
  }

  button {
    font-size: 20px;
    font-weight: bold;
  }




</style>